import { useState } from 'react';
import { useNotification } from '../components/NotificationProvider';
import ThemeToggle from '../components/ThemeToggle';

function Settings({ isDarkMode, onToggleTheme }) {
  const [settings, setSettings] = useState({
    theme: 'light',
    language: 'ru',
    notifications: true,
    autoSave: true
  });

  const { showNotification } = useNotification();

  const handleSettingChange = (key, value) => {
    const newSettings = {
      ...settings,
      [key]: value
    };
    setSettings(newSettings);
    localStorage.setItem('appSettings', JSON.stringify(newSettings));
  };

  const exportData = () => {
    const technologies = localStorage.getItem('technologies');
    const dataStr = JSON.stringify(JSON.parse(technologies || '[]'), null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    
    const link = document.createElement('a');
    link.href = URL.createObjectURL(dataBlob);
    link.download = 'technologies-backup.json';
    link.click();
    showNotification('  ', 'success');
  };

  const importData = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const data = JSON.parse(e.target.result);
          localStorage.setItem('technologies', JSON.stringify(data));
          showNotification('  ', 'success');
          window.location.reload();
        } catch (error) {
          showNotification('   :   ', 'error');
        }
      };
      reader.readAsText(file);
    }
  };

  const clearAllData = () => {
    if (window.confirm(' ,     ?    .')) {
      localStorage.removeItem('technologies');
      showNotification('  ', 'warning');
      window.location.reload();
    }
  };

  const addTestData = () => {
    const testTechnologies = [
      {
        id: 1,
        title: "React",
        description: "JavaScript     ",
        status: "in-progress",
        notes: "   .    !",
        createdAt: new Date('2024-01-15').toISOString()
      },
      {
        id: 2,
        title: "TypeScript",
        description: "   JavaScript",
        status: "completed",
        notes: "  ,   . Generics -  !",
        createdAt: new Date('2024-02-01').toISOString()
      },
      {
        id: 3,
        title: "Node.js",
        description: "  JavaScript   ",
        status: "not-started",
        notes: "   -   REST API",
        createdAt: new Date('2024-02-10').toISOString()
      },
      {
        id: 4,
        title: "MongoDB",
        description: " NoSQL  ",
        status: "in-progress",
        notes: "    .    -  !",
        createdAt: new Date('2024-02-20').toISOString()
      },
      {
        id: 5,
        title: "Docker",
        description: "   ",
        status: "not-started",
        notes: "      ",
        createdAt: new Date('2024-03-01').toISOString()
      },
      {
        id: 6,
        title: "GraphQL",
        description: "   API   ",
        status: "completed",
        notes: "     .   REST!",
        createdAt: new Date('2024-01-20').toISOString()
      }
    ];

    localStorage.setItem('technologies', JSON.stringify(testTechnologies));
    showNotification('  ', 'success');
    window.location.reload();
  };

  const handleSave = () => {
    showNotification(' ', 'success');
  };

  return (
    <div className="page">
      <div className="page-header">
        <h1> </h1>
      </div>

      <div className="settings-container">
        <div className="settings-section">
          <h3> </h3>
          
          <div className="setting-item">
            <label> :</label>
            <div style={{ display: 'flex', alignItems: 'center', gap: '10px', marginTop: '10px' }}>
              <span></span>
              <ThemeToggle isDarkMode={isDarkMode} onToggle={onToggleTheme} />
              <span></span>
            </div>
            <p>      </p>
          </div>

          <div className="setting-item">
            <label>:</label>
            <select 
              value={settings.language}
              onChange={(e) => handleSettingChange('language', e.target.value)}
            >
              <option value="ru"></option>
              <option value="en">English</option>
            </select>
          </div>
        </div>

        <div className="settings-section">
          <h3></h3>
          
          <div className="setting-item">
            <label>
              <input
                type="checkbox"
                checked={settings.notifications}
                onChange={(e) => handleSettingChange('notifications', e.target.checked)}
              />
               
            </label>
          </div>

          <div className="setting-item">
            <label>
              <input
                type="checkbox"
                checked={settings.autoSave}
                onChange={(e) => handleSettingChange('autoSave', e.target.checked)}
              />
               
            </label>
          </div>
        </div>

        <div className="settings-section">
          <h3> </h3>
          
          <div className="setting-item">
            <button onClick={addTestData} className="btn btn-third">
                
            </button>
            <p className="setting-description">
                   
            </p>
          </div>

          <div className="setting-item">
            <button onClick={exportData} className="btn btn-primary">
               
            </button>
            <p className="setting-description">
                      JSON
            </p>
          </div>

          <div className="setting-item">
            <label className="file-input-label">
              <input
                type="file"
                accept=".json"
                onChange={importData}
                style={{ display: 'none' }}
              />
              <span className="btn btn-secondary"> </span>
            </label>
            <p className="setting-description">
                    
            </p>
          </div>

          <div className="setting-item">
            <button onClick={clearAllData} className="btn btn-danger">
                
            </button>
            <p className="setting-description warning">
              :    !     .
            </p>
          </div>
        </div>
      </div>

      <div className="form-actions">
        <button className="btn btn-primary" onClick={handleSave}>
           
        </button>
      </div>
    </div>
  );
}

export default Settings;